<template>
  <div class="pa-4">
    <h1>主题切换</h1>
    <v-btn
      color="primary"
      class="mr-2"
      @click="changeTheme('theme1')"
    >
      主题1
    </v-btn>
    <v-btn
      color="primary"
      class="mr-2"
      @click="changeTheme('theme2')"
    >
      主题2
    </v-btn>
    <v-btn
      color="primary"
      class="mr-2"
      @click="changeTheme('theme0')"
    >
      默认
    </v-btn>
    <hr>
    <v-btn
      v-for="(item, index) in keyList"
      :key="index"
      :color="item"
      class="mr-2"
    >
      {{ item }}
    </v-btn>
  </div>
</template>

<script>
import { themes } from './theme';

export default {

  data() {
    return {
      keyList: Object.keys(themes.theme1),
    };
  },
  methods: {
    changeTheme(themeKey) {
      const theme = themes[themeKey];
      console.log(themeKey, theme, this.$vuetify.theme.themes);
      if (theme) {
        this.$vuetify.theme.themes.light = theme;
      }
      console.log({ ...this.$vuetify.theme.themes.light});
    },
  },
}
</script>

